<template>
    <FloatingPanel title="គម្រោងតាមដងទន្លេមេគង្គ" :width="width" :height="height"  @close="onClose">
        <div class="command">
            ចម្ងាយពីទន្លេ៖ 
            <input class="buffer-input" type="text" id="buffer-km" v-model="bufferSize" @keypress="isNumber($event)">
            គ.ម
            <input class="run-button" type="button" value="Run" @click="onRun">
            <input class="only-sand-checkbox" type="checkbox" v-model="onlySandProject" name="check" id="check1">
            <label for="check1">យកតែគម្រោងខ្សាច់សំណង់ និងល្បាយខ្សាច់</label>
        </div>


        <div class="list-title">{{title}}</div>

        <v-data-table 
            :headers="headers" 
            :items="filteredProjects" 
            :loading="isLoading"
            loading-text="សូមរងចាំ... (រយៈពេល ១ ទៅ ២ នាទី...)"
            :items-per-page="5"
            height="400"
            />

    </FloatingPanel>
</template>

<script>
import FloatingPanel from "@/components/FloatingPanel.vue";
import func from '@/scripts/helpers.js';
import store from "../store";
import Table from '@/components/Table.vue';
import * as worker from '../scripts/worker-api';


export default {
    name: 'SandProjects',

    props:{
        width: {
            type: Number,
            default: 400
        },
        height: {
            type: Number,
            default: 200
        }
    },

    components: {
        FloatingPanel,
        Table
    },

    data() {
        const river = func.geojson.empty();
        const allProjects = store.getters.geojson.features;
        const projects = func.geojson.features2DataTable(allProjects).filter(p => p.case===1);
        return {
            river,
            bufferedZone: river,
            isLoading: true,
            bufferSize: 0.1,
            onlySandProject: false,
            projects,
            
            headers:[
                { text: 'ម្ចាស់គម្រោង (KH)', value: 'owner_kh' },
                { text: 'ម្ចាស់គម្រោង (EN)', value: 'owner_en' },
                { text: 'ប្រភេទ', value: 'type' },
                { text: 'ខេត្ត', value: 'loc_khet' },
            ],
            
        }
    },
    
    async beforeCreate() {
        this.isLoading = true;
        const river = await func.fetch.riverAsync();
        this.bufferedZone = river;
        // this.$emit('data-river', river);
        const projects = func.geojson.create(this.projects.map(p => p.feature));
        this.$emit('data-projects', projects);
        this.isLoading = false;
    },

    computed:{
        title(){
            const onRiverOnly = this.bufferSize < 0.2;
            const projectCount = this.filteredProjects.length;
            const projectCountKh = func.en2kh(projectCount);
            const withSandTitle = this.onlySandProject? 'ខ្សាច់​សំណង់ និង​ល្បាយ​ខ្សាច់ ': '';
            if(onRiverOnly){ return `គម្រោង​${withSandTitle} នៅលើ​ដងទន្លេ​មេគង្គ ` + (projectCount>0? `(${projectCountKh}គម្រោង)`: ''); }

            const bufferSize = func.en2kh(this.bufferSize.toString());
            return `គម្រោង​​${withSandTitle}នៅលើ​ដងទន្លេ​មេគង្គ និងជុំវិញ ${bufferSize}គ.ម ` + (projectCount>0? `(${projectCountKh}គម្រោង)`: '');
        },
        filteredProjects:{
            get() { return this.onlySandProject? this.projects.filter(p => func.isSandProject(p)) : this.projects; },
            set(value) { this.projects = value; }
        },
        isEmptyRiver(){
            return func.geojson.isEmpty(this.river);
        },
    },

    methods:{
        isNumber: function(evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        async onRun(){
            // TODO: re-check data types: GEOJESON|[Features]|TableData
            this.isLoading = true;
            this.projects = [];
            const projects = store.getters.geojson;
            const features = projects.features;
            const buffered = await worker.bufferAsync(this.bufferedZone, this.bufferSize);
            this.bufferedZone = func.geojson.create(buffered.features, 'mekong');

            const selected = await worker.filterByPolygonAsync(features, buffered);
            this.filteredProjects = func.geojson.features2DataTable(selected);
            this.isLoading = false;
        },

        onClose(){
            this.$emit('data-projects', func.geojson.empty());
            this.$emit('close');
        }
    },

    watch:{
        filteredProjects() {
            const selectedGeo = func.geojson.dataTable2GeoJson(this.filteredProjects);
            this.$emit('data-projects', selectedGeo);
        },
        bufferedZone(){
            this.$emit('data-river', this.bufferedZone);
        }
    }


}
</script>

<style scoped>
.loading {
    text-align: center;
    margin: 9em;
}

.command {
    margin: 5px 10px;
}

.buffer-input {
    width: 50px;
    height: 23px;
    text-align: center;
    border: 1px solid gray;
}

.run-button {
    width: 50px;
    height: 23px;
    text-align: center;
    background-color: green;
    color: aliceblue;
    border-radius: 3px;
    border: 1px solid aliceblue;
    margin: 0px 10px;
}
.only-sand-checkbox {
    margin: 0px 5px;
}

.list-title {
    font-size: 14px ;
    font-family: 'Moul', Arial, Helvetica, sans-serif, cursive;
    text-align: center;
    background-color: white;
    padding: 10px;
}
</style>